@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap");

.App {
  height: 100vh;
}

.script-font {
  font-family: "Great Vibes", cursive;
}

.theme-color-background {
  background-color: orange;
}

.theme-color {
  color: orange;
}

.logo {
  cursor: pointer;
}

.post {
  width: 600px;
  height: 400px;
}

.posts {
  display: grid;
  grid-auto-flow: column;
  overflow-y: auto;
  padding: 25px;
  margin: 25px;
}

.footer {
  margin: 0;
  padding-bottom: 35px;
}

.footer ul {
  padding: 15px;
  list-style: none;
}

.footer span {
  margin: 15px;
}

.footer ul li {
  padding: 10px;
}

.card-image img {
  width: 356px;
  border-radius: 16px;
}

.post-by-location h5, .post-by-location br, .post-by-location hr {
  margin: 0;
}